// BarChart.js
import * as d3 from "d3";
import React, { useRef, useEffect } from "react";

const BarChart = ({ width, height, data }) => {
	const ref = useRef();
	const circleRef = useRef();

	const marginTop = 0; // top margin, in pixels
	const marginRight = 0; // right margin, in pixels
	const marginBottom = 0; // bottom margin, in pixels
	const marginLeft = 0; // left margin, in pixels

	useEffect(() => {
	function AreaChart(
		data,
		{
		x = ([x]) => x, // given d in data, returns the (temporal) x-value
		y = ([, y]) => y, // given d in data, returns the (quantitative) y-value
		defined, // given d in data, returns true if defined (for gaps)
		curve = d3.curveBasis, // method of interpolation between points
		xType = d3.scaleUtc, // type of x-scale
		xDomain, // [xmin, xmax]
		xRange = [marginLeft, width - marginRight], // [left, right]
		yType = d3.scaleLinear, // type of y-scale
		yDomain, // [ymin, ymax]
		yRange = [height - marginBottom, marginTop], // [bottom, top]
		yFormat, // a format specifier string for the y-axis
		yLabel, // a label for the y-axis
		} = {}
	) {
		// Compute values.
		const X = d3.map(data, x);
		const Y = d3.map(data, y);
		const I = d3.range(X.length);

		// Compute which data points are considered defined.
		if (defined === undefined)
		defined = (d, i) => !isNaN(X[i]) && !isNaN(Y[i]);
		const D = d3.map(data, defined);

		// Compute default domains.
		if (xDomain === undefined) xDomain = d3.extent(X);
		if (yDomain === undefined) yDomain = [0, d3.max(Y)];

		// Construct scales and axes.
		const xScale = xType(xDomain, xRange);
		const yScale = yType(yDomain, yRange);
		const xAxis = d3
		.axisBottom(xScale)
		.ticks(width / 80)
		.tickSizeOuter(0);
		const yAxis = d3.axisLeft(yScale).ticks(height / 40, yFormat);

		// Construct an area generator.
		// const area = d3.area()
		//	 .defined(i => D[i])
		//	 .curve(curve)
		//	 .x(i => xScale(X[i]))
		//	 .y0(yScale(0))
		//	 .y1(i => yScale(Y[i]));

		const Line = d3
		.line()
		.defined((i) => D[i])
		.curve(curve)
		.x((i) => xScale(X[i]))
		.y((i) => yScale(Y[i]));

		const svg = d3
		.select(ref.current)
		.attr("width", width)
		.attr("height", height)
		.attr("viewBox", [0, 0, width, height])
		.attr("style", "max-width: 100%; height: auto; height: intrinsic;");

		svg
		.append("g")
		.attr("transform", `translate(${marginLeft},0)`)
		.call(yAxis)
		.call((g) => g.select(".domain").remove())
		.call((g) =>
			g
			.selectAll(".tick line")
			.clone()
			.attr("x2", width - marginLeft - marginRight)
			.attr("stroke-opacity", 0.1)
		)
		.call((g) =>
			g
			.append("text")
			.attr("x", -marginLeft)
			.attr("y", 10)
			.attr("fill", "none")
			.attr("text-anchor", "start")
			.text(yLabel)
		);

		svg
		.append("path")
		.attr("fill", "transparent")
		.attr("stroke", "#f2f2f2")
		.attr("stroke-width", "3")
		.attr("d", Line(I));

		svg
		.append("g")
		.attr("transform", `translate(0,${height - marginBottom})`)
		.call(xAxis);

		return svg.node();
	}
	AreaChart(data, {
		x: (d, idx) => idx,
		y: (d) => d,
		yLabel: "Total Visitors",
		color: "steelblue",
	});
	}, [data, width, height]);

	return (
	<div className="chart">
		<svg ref={ref}>
		<g ref={circleRef}></g>
		</svg>
	</div>
	);
};

export default BarChart;
